#card {
  width: 33%;
  padding: 70px 30px;
  display: flex;
  flex-direction: column;
  text-align: left;
}

#card:hover {
  background-color: #feb633;
  cursor: pointer;
}

#card h3 {
  font-size: 22px;
}

#card .light-titulo h3 {
  color: #32333a;
}

#card .dark-titulo h3 {
  color: #edecec;
}

.dark-mode-cards {
  border: 1px solid #ffffff0d;
}

.light-mode-cards {
  border: 1px solid #edecec;
}

#card p {
  color: #8a8fa3;
}

#card h3 {
  margin: 10px 0;
}

#card p:last-of-type {
  margin-top: 20px;
}

@media (max-width: 768px) {
  #card {
    width: 100%;
  }
}
