.banner {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  margin-top: 80px;
}

.img-banner {
  background-image: url("../../assets/banner.png");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  width: 50%;
  max-width: 800px;
  height: 80vh;
  padding-left: 20px;
}

.dark-mode-banner {
  background-color: #252525;
}

.light-mode-banner {
  background-color: #fff;
}

.texto-banner {
  display: flex;
  flex-direction: column;
  justify-content: center;
  color: #022144;
  background-color: #fafbfc;
  width: 50%;
  height: 80vh;
  max-width: 800px;
  padding: 0 20px;
}

.scale-up-hor-left {
  -webkit-animation: scale-up-hor-left 0.8s cubic-bezier(0.39, 0.575, 0.565, 1)
    both;
  animation: scale-up-hor-left 0.8s cubic-bezier(0.39, 0.575, 0.565, 1) both;
}

.texto-banner p {
  letter-spacing: 3px;
  font-size: 12px;
}

.texto-banner h2 {
  font-size: 40px;
  font-weight: 500;
}

.texto-banner span {
  font-size: 40px;
  font-weight: 900;
  word-spacing: 5px;
}

@media (max-width: 768px) {
  .img-banner {
    display: none;
  }

  .texto-banner {
    width: 100%;
  }
}

/**
 * ----------------------------------------
 * animation scale-up-hor-left
 * ----------------------------------------
 */
@-webkit-keyframes scale-up-hor-left {
  0% {
    -webkit-transform: scaleX(0.4);
    transform: scaleX(0.4);
    -webkit-transform-origin: 0% 0%;
    transform-origin: 0% 0%;
  }
  100% {
    -webkit-transform: scaleX(1);
    transform: scaleX(1);
    -webkit-transform-origin: 0% 0%;
    transform-origin: 0% 0%;
  }
}
@keyframes scale-up-hor-left {
  0% {
    -webkit-transform: scaleX(0.4);
    transform: scaleX(0.4);
    -webkit-transform-origin: 0% 0%;
    transform-origin: 0% 0%;
  }
  100% {
    -webkit-transform: scaleX(1);
    transform: scaleX(1);
    -webkit-transform-origin: 0% 0%;
    transform-origin: 0% 0%;
  }
}
