#topo {
  position: fixed;
  height: 80px;
  width: 100%;
  margin-top: -80px;
  z-index: 1;
}

.obj-topo {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  margin: 0 auto;
  max-width: 1600px;
}
.btn-img {
  width: 25px;
  height: 25px;
}
.logo {
  max-height: 35px;
}
.btn-mode {
  padding: 8px;
  display: grid;
  cursor: pointer;
  box-shadow: 1px 1px 2px #747272;
}
.btn-mode:hover {
  transform: scale(1.1);
}

.dark-mode-topo {
  background-color: #252525;
  border-bottom: 1px solid #ffffff0d;
}

.light-mode-topo {
  background-color: #fff;
}

.dark-mode-btn {
  background-color: #252525;
  border-radius: 25px;
  border: 2px solid #fff;
}

.light-mode-btn {
  background-color: #fff;
  border-radius: 25px;
  border: 2px solid #747272;
}
