.experiencia {
  margin: 0 auto;
  text-align: center;
  padding: 60px 10px;
}

.experiencia-texto h2 {
  font-size: 50px;
  line-height: 70px;
  position: relative;
  font-weight: bold;
}

.experiencia-cards {
  display: flex;
  justify-content: center;
  align-items: stretch;
  max-width: 1600px;
  margin: 0 auto;
}

.experiencia-texto h2::before {
  content: "";
  position: absolute;
  bottom: -2px;
  left: 50%;
  transform: translateX(-50%);
  width: 10%;
  height: 2px;
  background-color: #feb633;
}

.experiencia-texto p {
  padding: 20px 10px 50px 10px;
  color: #8a8fa3;
}

.dark-mode-experiencia {
  background-color: #393838;
  color: #f6f8fc;
}

.light-mode-experiencia {
  background-color: #f6f8fc;
  color: #393838;
}

@media (max-width: 768px) {
  .experiencia {
    width: 100%;
  }

  .experiencia-texto h2 {
    font-size: 45px;
    line-height: 60px;
  }

  .experiencia-cards {
    flex-direction: column;
  }
}
