.rodape {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-top: 60px;
}

.logo {
  width: auto;
  height: auto;
}

.rodape p {
  max-width: 600px;
  text-align: center;
  color: #8a8fa3;
  padding: 30px 10px;
}
.rodape p:last-of-type {
  max-width: 100%;
  text-align: center;
  color: #8a8fa3;
  padding: 20px 0;
  border: 1px solid #ffffff0d;
  width: 100%;
}

.rodape-links {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 5px 10px 60px 10px;
}

.icone {
  height: 45px;
}

.rodape-links .link {
  text-decoration: none;
  cursor: pointer;
  padding: 5px;
  transition: transform 2s ease-in-out;
  transform-origin: center;
}
.rodape-links .link:hover {
  transform-origin: ;
  animation: shake 2s cubic-bezier(0.26, 0.7, 0.34, 0.76) infinite;
}

.assinatura {
  color: #feb633;
  font-weight: 500;
  font-size: 14px;
}

.dark-mode-rodape {
  background-color: #252525;
}

.light-mode-rodape {
  background-color: #fff;
}

@media (max-width: 375px) {
  .rodape-links {
    flex-wrap: wrap;
  }
}

/* Animação do link:hover */

@keyframes shake {
  0% {
    transform: scale(1.2) rotate(0deg);
  }
  25% {
    transform: scale(1.2) rotate(-45deg);
  }
  50% {
    transform: scale(1.2) rotate(0deg);
  }
  75% {
    transform: scale(1.2) rotate(45deg);
  }
  100% {
    transform: scale(1.2) rotate(0deg);
  }
}
